export default class Linea extends Component {
    render() {
        return <div className='container p-5'>
            <div className="">
                <TituloSeccion
                    imagen={logoLinea}
                    titulo={"Lnea de tiempo"}
                />
            </div>
            <div>
                <ul className='timeline'>
                    <li>
                        <strong><p>Ingreso Analista Programador (2017)</p></strong>
                        <p>Comienzo carrera en CFT Inacap, estudiando las bases de la programacin y el pseudocdigo</p>
                    </li>
                    <li>
                        <strong><p>Tercer Semestre (2018)</p></strong>
                        <p>Diseo Web, diseo de aplicaciones mviles y bases de datos relacionales</p>
                    </li>
                    <li>
                        <strong><p>Inicio proyecto 'Administracin Colegio' en PHP (2018)</p></strong>
                        <p>Se comienza con toma de requerimientos y primeros bocetos de la aplicacin</p>
                    </li>
                    <li>
                        <strong><p>Cuarto Semestre (2018)</p></strong>
                        <p>Desarrollo en lenguaje C#, mayor profundizacin de conocimientos y realizacin de proyectos con Arduino</p>
                    </li>
                    <li>
                        <strong><p>Quinto Semestre (2019)</p></strong>
                        <p>Desarrollo de 'Gestin Biblioteca' como proyecto final, con generacin de la base de datos, creacin de pantallas y lanzamiento para ser presentado en clases </p>
                        <p>Adems, se hizo la presentacin del proyecto 'Administracin Colegio' y se concluy con xito el proceso de entrega </p>
                    </li>
                    <li>
                        <strong><p>Prctica Profesional (2019)</p></strong>
                        <p>Desde agosto hasta noviembre realice mi prctica en la empresa Innova SPA, como soporte tcnico de software Softland</p>
                    </li>
                    <li>
                        <strong><p>Titulado (2019)</p></strong>
                    </li>
                </ul>
            </div>

        </div>
    }
}